import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApiService } from '../api.service';
import { async } from '@angular/core/testing';
import { StarRatingComponent } from 'ng-starrating';
import { Validators, FormBuilder } from '@angular/forms';
import { toBase64String } from '@angular/compiler/src/output/source_map';

@Component({
  selector: 'app-resource',
  templateUrl: './resource.component.html',
  styleUrls: ['./resource.component.css']
})
export class ResourceComponent implements OnInit {

  query
  width1:any;
  width2:any;
  width3:any;
  width4:any;
  width5:any;
  subcat;
  back1:any;
  back2:any;
  back3:any;
  back4:any;
  back5:any;
  subject:any;
  r:any;
  level:any;
  topic2:any;
  reso:any;
  lvl;
  show:any;
  topic;
  type;
  empty = false;
  data: any;
  comments: any;
  ratings: any;
  onlyRating = []
  onlyRating2 = []
username:any
  list = true;
  rate = 0;
  modal=false;
  modelData:any;
  subc: any;
  top: any;
  subtop: any;
  resource:any;
  rateCommentForm:any;
  constructor(private route: ActivatedRoute, private api: ApiService,private fb:FormBuilder) {
    if (localStorage.getItem('Token') == null || localStorage.getItem('Token') == undefined) {
      window.location.href='/login'
    }
    this.query = this.route.queryParams
    this.subcat = this.query._value.subcat
    this.lvl = this.query._value.lvl
    this.topic = this.query._value.topic
    this.type = this.query._value.type
  }

  ngOnInit() {
    this.getResources()
    this.rateCommentForm=this.fb.group({
      comment: ['',[Validators.required]],
    })
    this.subc = localStorage.getItem("subcat");
    this.top=localStorage.getItem("top");
    this.subtop = localStorage.getItem("subtop")
    this.resource="/resource"+window.location.search;
    localStorage.setItem("resource",this.resource)
   this.subject = localStorage.getItem("Subject");
   this.level = localStorage.getItem("level");
   this.topic2 = localStorage.getItem("topic");
   this.reso = localStorage.getItem("Resource");
   this.r ="resource"+window.location.search;
  }

  resourceDetails(resource){
    // console.log(resource)
    this.modelData=resource

    this.modal=true;

    this.width1 = (this.modelData.single_rating_counter.star1_count/this.modelData.rating_counter) * 100 || 0;
    this.width2 = (this.modelData.single_rating_counter.star2_count/this.modelData.rating_counter) * 100 || 0;
    this.width3 = (this.modelData.single_rating_counter.star3_count/this.modelData.rating_counter) * 100 || 0;
    this.width4 = (this.modelData.single_rating_counter.star4_count/this.modelData.rating_counter) * 100 || 0;
    this.width5 = (this.modelData.single_rating_counter.star5_count/this.modelData.rating_counter) * 100 || 0;
}
  hideModal(){
    this.modal=false
  }

  async getResources() {
    this.api.loader()
    let data = await this.api.getData(`navigate/?sub_id=${this.subcat}&lev_id=${this.lvl}&tpc_id=${this.topic}`)
    data.subscribe((res: any) => {
      this.data = res.resources.filter((r: any) => r.resource_type == this.type)

      for (let i = 0; i < this.data.length; i++) {
        this.comments = res.comments.filter(c => c.resource == this.data[i].id)
        // this.ratings = res.ratings.filter(c => c.resource == this.data[i].id)

       this.ratings = res.ratings;
        let twoComment: any;
        if (this.comments.length > 2) {
          twoComment=this.comments.slice(0, 2);
        } else {
          twoComment = Array.from(this.comments)
        }
        // console.log(twoComment)
        this.data[i] = { ...this.data[i], comments: this.comments, fewComment: twoComment }

      }

    //   for (let i = 0; i < this.data.length; i++) {
    //     if(this.comments[i].commenter){
    //     // this.onlyRating = this.ratings.filter(r => r.rated_by != this.comments[i].commenter)
    //   }
    // }

    // console.log(this.onlyRating)
      this.api.noloader()
      // console.log(this.data)
      if (this.data.length == 0) {
        this.empty = true
      }
    },
    (err:any)=>{
if(err){
  this.empty= true;
  this.api.noloader();
}
    }
    )
  }
  www(d){
    let n = d.startsWith("www");
  if(n){

  return true;
  }
  else {
    return false;
  }

  }
  http(d){
    let n = d.startsWith("http");
    if(n){
      return true;
      }
      else {
        return false;
      }

  }

  getname = async (auther) => {
    let data = await this.api.getData('get_teachers/?t_id=' + auther)
    data.subscribe((res: any) => {
this.username = res.get_username;
      return true;
    },(err:any)=>{
      if(err){
        return false;
      }
          }
    )
  }



  followAuther = async (auther) => {
    this.api.loader()
    let data = await this.api.getData('follow_author/?aut_id=' + auther)
    data.subscribe((res: any) => {
      console.log(res)
      this.api.noloader()
      this.api.message(res.OK)
    },(err:any)=>{
      if(err){
      this.api.noloader()
      this.api.message("You cannot follow a student");
      }
          })
  }


  getRating(rater, resource) {
    this.api.loader()
    let rate: any = this.ratings.filter(r => (r.rated_by == rater) && (r.resource == resource))
    // console.log(rate[0])
    if (rate[0] != undefined) {
      this.api.noloader()
      return rate[0].rating
    } else {
      this.api.noloader()
      return 0;
    }
  }

  layoutChange(n) {
    if (n == 1) {
      this.list = false
    } else {
      this.list = true
    }
  }

  onRate($event: { oldValue: number, newValue: number, starRating: StarRatingComponent }) {
    this.rate = $event.newValue
  }

  async ratingHandler() {
    this.api.loader()
    if (this.rate != 0 && this.rateCommentForm.controls['comment'].value != '') {
      let dataobj: any = { resource_id: this.modelData.id }
      if (this.rate != 0) {
        dataobj = { ...dataobj, rating: this.rate }
      }
      if (this.rateCommentForm.controls['comment'].value != '') {
        dataobj = { ...dataobj, comment: this.rateCommentForm.controls['comment'].value }
      }
      let data = await this.api.postData('post_comment_rating/', dataobj)
      data.subscribe((res: any) => {
        console.log(res)
        this.rate = 0;
        this.api.noloader()
        this.rateCommentForm.reset();
        window.location.reload();
      },(err:any)=>{
        if(err){
        this.api.noloader()
        this.api.message("You cannot comment on a resource twice");
        }
            })
    } else {
      this.api.noloader()
      this.api.message("All Field are required.")
      return null
    }
  }


  saveLink = async (id) => {
    this.api.loader()
    let data = await this.api.getData(`save_resource?rsc_id=${id}`)
    data.subscribe((res: any) => {
      // console.log(res)
      this.api.noloader()
      this.api.message(res.OK)
      // this.data = res

    })
  }

}
