import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';

@Component({
  selector: 'app-pending',
  templateUrl: './pending.component.html',
  styleUrls: ['./pending.component.css']
})
export class PendingComponent implements OnInit {

  data: any;
  empty=false
 count :any
  constructor(private api: ApiService) {
    if (localStorage.getItem('Token') == null || localStorage.getItem('Token') == undefined) {
      window.location.href='/login'
    }
   }

  ngOnInit() {
    this.getPending()
  }

  async getPending() {
    this.api.loader()
    let data = await this.api.getData('get_pending/')
    data.subscribe((res: any) => {
      // console.log(res)
      this.api.noloader()

      if(res.length==0){
        this.empty=true
      }else{
        this.data = res
        this.count = res.length;
        localStorage.setItem("count",JSON.stringify(this.count))

      }
    })
  }
  www(d){
    let n = d.startsWith("www");
  if(n){

  return true;
  }
  else {
    return false;
  }

  }
  http(d){
    let n = d.startsWith("http");
    if(n){
      return true;
      }
      else {
        return false;
      }

  }
  async disapp(id,i){
    this.api.loader()
    let data = await this.api.getData('delete_resource?rsc_id='+id)
    data.subscribe((res:any)=>{
      // console.log(res)
      this.api.noloader()
      this.count --;
      localStorage.setItem("count",JSON.stringify(this.count))
      this.api.message('Disapproved')
      this.data.splice(i, 1);
    }
    ,
    (err:any)=>{
if(err){
// console.log(err)
}
})
  }
  disapprove(item){
document.querySelector(item).style.display = "none"
  }

  async approve(id,i){
    this.api.loader()
    let data = await this.api.getData('approve_resource/?rsc_id='+id)
    data.subscribe((res:any)=>{
      // console.log(res)
      this.api.noloader()
      this.count --;
      localStorage.setItem("count",JSON.stringify(this.count))
      this.api.message('Approved')
      this.data.splice(i, 1);
    })
  }

}
