import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';
import { async } from '@angular/core/testing';
import { Validators, FormBuilder } from '@angular/forms';
import { StarRatingComponent } from 'ng-starrating';

@Component({
  selector: 'app-top-rated',
  templateUrl: './top-rated.component.html',
  styleUrls: ['./top-rated.component.css']
})
export class TopRatedComponent implements OnInit {

  data: any;
  empty = false
  ratings: any;
  onlyRating = []
  comments: any;
  modelData: any
  width1:any;
  width2:any;
  width3:any;
  width4:any;
  width5:any;
  show:any;
  modal = false
  rateCommentForm: any
  rate = 0
  list = true;
  constructor(private api: ApiService, private fb: FormBuilder) {
    if (localStorage.getItem('Token') == null || localStorage.getItem('Token') == undefined) {
      window.location.href='/login'
    }
   }

  ngOnInit() {
    this.topRated();
    this.rateCommentForm = this.fb.group({
      comment: ['', [Validators.required]],
    })
  }
  topRated = async () => {
    this.api.loader()
    let data = await this.api.getData('sort_resources/?rating=True')
    data.subscribe((res: any) => {
      this.data = res.resources;
   console.log(this.data)
   for (let i = 0; i < this.data.length; i++) {
    this.comments = res.comments.filter(c => c.resource == this.data[i].id)
    // this.ratings = res.ratings.filter(c => c.resource == this.data[i].id)

   this.ratings = res.ratings;
    let twoComment: any;
    if (this.comments.length > 2) {
      twoComment=this.comments.slice(0, 2);
    } else {
      twoComment = Array.from(this.comments)
    }
    // console.log(twoComment)
    this.data[i] = { ...this.data[i], comments: this.comments, fewComment: twoComment }

    console.log(this.data)
  }

      // for (let i = 0; i < this.data.length; i++) {
      //   // this.onlyRating = this.ratings.filter(r => r.rated_by != this.comments[i].commenter)
      // }
      this.api.noloader()
      console.log(this.data)
      if (this.data.length == 0) {
        this.empty = true
      }
    })
  }

www(d){
  let n = d.startsWith("www");
if(n){

return true;
}
else {
  return false;
}

}
http(d){
  let n = d.startsWith("http");
  if(n){
    return true;
    }
    else {
      return false;
    }

}

  getRating(rater, resource) {
    let rate: any = this.ratings.filter(r => (r.rated_by == rater) && (r.resource == resource))
    if (rate[0] != undefined) {
      return rate[0].rating
    } else {
      return 0;
    }
  }
  layoutChange(n) {
    if (n == 1) {
      this.list = false
    } else {
      this.list = true
    }
  }
  resourceDetails(resource){
    // console.log(resource)
    this.modelData=resource

    this.modal=true;
    this.width1 = (this.modelData.single_rating_counter.star1_count/this.modelData.rating_counter) * 100 || 0;
    this.width2 = (this.modelData.single_rating_counter.star2_count/this.modelData.rating_counter) * 100 || 0;
    this.width3 = (this.modelData.single_rating_counter.star3_count/this.modelData.rating_counter) * 100 || 0;
    this.width4 = (this.modelData.single_rating_counter.star4_count/this.modelData.rating_counter) * 100 || 0;
    this.width5 = (this.modelData.single_rating_counter.star5_count/this.modelData.rating_counter) * 100 || 0;
}
  hideModal() {
    this.modal = false
  }

  onRate($event: { oldValue: number, newValue: number, starRating: StarRatingComponent }) {
    this.rate = $event.newValue
  }

  saveLink = async (id) => {
    this.api.loader()
    let data = await this.api.getData(`save_resource?rsc_id=${id}`)
    data.subscribe((res: any) => {
      console.log(res)
      this.api.noloader()
      this.api.message(res.OK)

    })
  }

  async ratingHandler() {
    this.api.loader()
    if (this.rate != 0 && this.rateCommentForm.controls['comment'].value != '') {
      let dataobj: any = { resource_id: this.modelData.id }
      if (this.rate != 0) {
        dataobj = { ...dataobj, rating: this.rate }
      }
      if (this.rateCommentForm.controls['comment'].value != '') {
        dataobj = { ...dataobj, comment: this.rateCommentForm.controls['comment'].value }
      }
      let data = await this.api.postData('post_comment_rating/', dataobj)
      data.subscribe((res: any) => {
        console.log(res)
        this.rate = 0;
        this.api.noloader()
        this.rateCommentForm.reset();
        window.location.reload();
      },(err:any)=>{
        if(err){
        this.api.noloader()
        this.api.message("You cannot comment on a resource twice");
        }
            })
    } else {
      this.api.noloader()
      this.api.message("All Field are required.")
      return null
    }
  }

  followAuther = async (auther) => {
    this.api.loader()
    let data = await this.api.getData('follow_author/?aut_id=' + auther)
    data.subscribe((res: any) => {
      console.log(res)
      this.api.noloader()
      this.api.message(res.OK)
    },(err:any)=>{
      if(err){
      this.api.noloader()
      this.api.message("You cannot follow a student");
      }
          })
  }
}
