import { Component, OnInit } from '@angular/core';
import { async } from '@angular/core/testing';
import { ApiService } from '../api.service';

@Component({
  selector: 'app-teachers-list',
  templateUrl: './teachers-list.component.html',
  styleUrls: ['./teachers-list.component.css']
})
export class TeachersListComponent implements OnInit {

  data: any;
  empty = false
  data2: any;
data3: any;
resources=[]
  constructor(private api: ApiService) {
    if (localStorage.getItem('Token') == null || localStorage.getItem('Token') == undefined) {
      window.location.href='/login'
    }
   }

  ngOnInit() {
    this.getTeacher();
  }

  getTeacher = async () => {
    this.api.loader()
    let data = await this.api.getData('follow_author/')
    data.subscribe((res: any) => {
      console.log(res)
      this.api.noloader()
      this.data=res.followed
      this.data2=res.resources;
      this.data3=res.Teachers;

    },
    (err:any)=>{
if(err){
  this.empty= true;
  this.api.noloader();
}
    })
  }

  check(x){
    this.resources=[]
    for (let d= 0; d <this.data2.length; d++){
      if(x == this.data2[d].author){
       this.resources.push(this.data2[d])}
      else{
        continue;
      }

    }
    if(this.resources.length == 0 ){
      return false;
    }
    else{
      return true;
    }
  }
www(d){
  let n = d.startsWith("www");
if(n){

return true;
}
else {
  return false;
}

}
http(d){
  let n = d.startsWith("http");
  if(n){
    return true;
    }
    else {
      return false;
    }

}

saveLink = async (id) => {
  this.api.loader()
  let data = await this.api.getData(`save_resource?rsc_id=${id}`)
  data.subscribe((res: any) => {
    console.log(res)
    this.api.noloader()
    this.api.message(res.OK)
    // this.data = res

  })
}
  followAuther = async (auther) => {
    this.api.loader()
    let data = await this.api.getData('follow_author/?aut_id=' + auther)
    data.subscribe((res: any) => {
      console.log(res)
      this.api.noloader()
      this.api.message(res.OK)
      window.location.reload();

    },(err:any)=>{
      if(err){
      this.api.noloader()
      this.api.message("You cannot follow a student");
      }
          })
  }
}
